<template>
  <div id="app" class="container">

    <h1>What PSI is that?</h1>

    <ul class="tab-nav">

      <li><a class="button"
          v-bind:class="{active: mode==0}"
          v-on:click="mode=0">Find by date</a></li>

      <li><a class="button"
          v-bind:class="{active: mode==1}"
          v-on:click="mode=1">Find by PSI/sprint</a></li>

    </ul>

    <div id="dateToPsi" class="row" v-if="mode==0">

      <div class="eight columns">
        <datepicker :inline="true" v-model="state.date" v-on: load="updatePsi" v-on:selected="updatePsi" v-on:input="updatePsi"></datepicker>
      </div>

      <div class="four columns">
        <span class="huge">{{state.psi.sprint}}<span v-if="state.psi.estimated">*</span></span>
        <div>{{state.psi.dates}}<span v-if="state.psi.estimated"> (estimated)</span></div>
      </div>

    </div>

    <div id="psiToDate" class="row" v-if="mode==1">

      <div class="six columns">
        <input type="number" required step=".1" v-model="psiInput" placeholder="PSI #"></input>
      </div>

      <div id="dates" class="six columns">
        <ul>
          <li v-for="i in sprintList">
            <strong>{{i.sprint}}</strong>:
            {{i.dates}}<span v-if="i.estimated"> (estimated)</span>
          </li>
        </ul>
      </div>

    </div>

  </div>
</template>

<script>
  import psi from '../psi'
  import Datepicker from 'vuejs-datepicker'

  var state = {
    date: new Date(),
    psi: psi.getPsi(this.date)
  }

  window.mystate = state

  export default {
    name: 'app',
    components: {
      Datepicker
    },
    data: function() {
      return {
        mode: 0,
        format: 'd MMMM yyyy',
        disabled: {},
        eventMsg: null,
        state: state,
        vModelExample: state.date,
        psiInput: '',
        sprintList: []
      }
    },
    methods: {
      updatePsi: function() {
        state.psi = psi.getPsi(state.date);
      }
    },
    watch: {
      psiInput(psiInput) {
        console.log(psi.getSprints(psiInput))
        this.sprintList = psi.getSprints(psiInput)
      }
    }
  }
</script>

<!-- CSS libraries -->
<style src="normalize.css/normalize.css"></style>
<style src="./skeleton.css"></style>


<!-- Scoped component css -->
<!-- It only affect current component -->
<style scoped>
  #app {
    text-align: center;
    padding-top: 4em;
  }

  input[type=number] {
    font-size: 8rem;
    height: 9rem;
    width: 3em;
    text-align: center;
  }

  .huge {
    font-size: 8rem;
    font-weight: bold;
  }

  ul {
    list-style-type: none;
  }


  ul.tab-nav {
      list-style: none;
      border-bottom: 1px solid #bbb;
      padding-left: 5px;
  }

  ul.tab-nav li {
      display: inline;
  }

  ul.tab-nav li a.button {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      margin-bottom: -1px;
      border-bottom: none;
  }

  ul.tab-nav li a.active.button {
      border-bottom: 1px solid #fff;
  }

  .tab-content .tab-pane {
      display: none;
  }

  .tab-content .tab-pane.active {
      display: block;
  }

</style>



