import psiData from './psi-data'
import moment from 'moment'

var stringify = function(data) {
  return data.psi + '.' + data.sprint;
}

var dateStr = function(date) {
  return moment(date).format('MMMM Do YYYY')
}

var datify = function(data) {
  return dateStr(data.start) + ' - ' + dateStr(data.end)
}

var lastSprint = function() {
  return psiData[psiData.length-1]
}

var forDisplay = function(sprint) {
  return {sprint: stringify(sprint), dates: datify(sprint), estimated: sprint.estimated}
}

var predictSprintByDate = function(rawDate) {
  var lastPsi = lastSprint().psi
  var startOfPredictionSpace = moment(lastSprint().end, 'YYYY/MM/DD').add(1, 'day')
  var date = moment(rawDate)
  var diff = moment.duration(date.diff(startOfPredictionSpace)).asWeeks()
  var psiDiff = Math.floor(diff/12.0)
  var psi = psiDiff + lastPsi+1;
  var sprint = Math.floor((diff%12.0)/2)+1
  var sprintStart = moment(startOfPredictionSpace).add(psiDiff*12+(sprint-1)*2, 'weeks')
  var sprintEnd = moment(startOfPredictionSpace).add(psiDiff*12+sprint*2, 'weeks').subtract(1, 'day')
  return forDisplay({psi: psi, sprint: sprint, start: sprintStart, end: sprintEnd, estimated: true})
}

var predictSprintsByPsi = function(psi) {
  var lastPsi = lastSprint().psi
  var endOfLastPsi = moment(lastSprint().end, 'YYYY/MM/DD')
  var startOfPredictionSpace = endOfLastPsi.add(1, 'day')
  if (Math.floor(psi) <= lastPsi) {
    return []
  }
  var ret = []
  var psiStart = startOfPredictionSpace.add(12*(Math.floor(psi)-lastPsi-1), 'weeks')
  if (Math.floor(psi) == psi) { // user asked for a whole PSI
    for (var i = 0; i < 6; i++) {
      var s = i+1
      var sprintStart = moment(psiStart).add(2*i, 'weeks').format('YYYY/MM/DD')
      var sprintEnd = moment(psiStart).add(2*s, 'weeks').subtract(1, 'day').format('YYYY/MM/DD')
      ret.push(forDisplay({psi: Math.floor(psi), sprint: s, start: sprintStart, end: sprintEnd, estimated: true}))
    }
    return ret
  } // assume user asked for a sprint
  var sprintId = psi.split('.')[1]
  if (Math.floor(sprintId) == sprintId && sprintId > 0 && sprintId <= 6) {
    var sprintStart = moment(psiStart).add(2*(sprintId-1), 'weeks').format('YYYY/MM/DD')
    var sprintEnd = moment(psiStart).add(2*(sprintId), 'weeks').subtract(1, 'day').format('YYYY/MM/DD')
    return [forDisplay({psi: Math.floor(psi), sprint: sprintId, start: sprintStart, end: sprintEnd, estimated: true})]
  }
  return []
}

var predictSprintsByDate = function(rawDate) {

}

var PSI = {

  getPsi(rawDate) {
    var date = moment(rawDate)
    for (var i in psiData) {
      var sprint = psiData[i];
      if (date.isBefore(sprint.start)) {
        return ''
      }
      if (date.isBetween(sprint.start, sprint.end, 'day', '[]')) {
        return forDisplay(sprint)
      }
    }
    return predictSprintByDate(rawDate)
    return ''
  },

  getSprints(id) {
    if (id.length < 2) {
      return []
    }
    var found = psiData.filter(function(sprint) {
      return stringify(sprint).startsWith(id)
    }).map(function(sprint) {
      return forDisplay(sprint)
    })
    if (found.length == 0) {
      return predictSprintsByPsi(id)
    }
    return found
  },

}

export default PSI



